<template>
  <div>
    <div class='item'>
      <el-card class='card' shadow="hover">
        <div class='user'>
          <span class='userAvatar'>
            <el-avatar shape="square" :size="80" :src="avatar"></el-avatar>
          </span>

          <span class='userInfo'>
            <div class='username'>{{usernameFormatted}}</div>
            <div class='userSchool'>{{schoolFormatted}}</div>
          </span>
        </div>

        <div class='content'>
          {{contentFormatted}}
        </div>

        <div class='footer'>
          <span class='open' @click='showDetail(postID)'>
            <span>{{$t('Hollow.ShowDetail')}}</span>
            <i class='el-icon-caret-bottom'></i>
          </span>

          <span class='status'>
            <span class='statusElement'>{{timeFormatted}}</span>

            <el-tooltip :content='this.comments + this.$t("Hollow.CommentsAmount")' placement='top-start'
              effect='light'>
              <span class='statusElement'>
                <el-image :src="require('@/../public/assets/comment.png')" class='image'></el-image>{{comments}}
              </span>
            </el-tooltip>

            <el-tooltip :content='this.like + this.$t("Hollow.LikeAmount")' placement='top-start' effect='light'>
              <span class='statusElement'>
                <el-image :src="require('@/../public/assets/like.png')" class='image'></el-image>{{like}}
              </span>
            </el-tooltip>
          </span>
        </div>
      </el-card>
    </div>
  </div>

</template>

<script>
export default {
  name: 'hollow-item',
  data () {
    return {
      avatar: ''
    }
  },
  props: {
    postID: { type: Number },
    username: { type: String },
    isNicked: { type: Number },
    time: { type: String },
    school: { type: Number },
    content: { type: String },
    like: { type: Number },
    comments: { type: Number },
    userid: { type: Number }
  },
  computed: {
    contentFormatted () {
      if (this.content && this.content.length > 40) {
        return this.content.substring(0, 40) + '......';
      }
      return this.content;
    },
    usernameFormatted () {
      return this.formatUsername(this.username, this.isNicked);
    },
    schoolFormatted () {
      return this.formatSchool(this.school);
    },
    timeFormatted () {
      return this.formatTime(this.time);
    },
  },
  methods: {
    showDetail (postID) {
      this.$bus.$emit('showHollowDetail', postID)
    }
  },
  mounted () {
    this.avatar = this.getUserAvatar(this.userid);
  }
}
</script>

<style scoped>
.item {
  margin-left: 10%;
  width: 80%;
}
.card {
  border-radius: 20px;
}
.user {
  display: flex;
}
.user .userInfo {
  margin-left: 20px;
  font-size: 20px;
}
.user .userInfo .username {
  font-size: 30px;
  margin-bottom: 10px;
}
.content {
  font-size: 20px;
  text-align: left;
  margin-top: 20px;
  margin-left: 15px;
}
.footer {
  text-align: left;
  margin-top: 20px;
  margin-left: 15px;
}
.statusElement {
  margin-left: 20px;
}
.open {
  font-size: 20px;
  margin-top: 20px;
  margin-bottom: 10px;
  cursor: pointer;
  color: rgb(61, 87, 235);
}
.footer .status {
  float: right;
  margin-right: 8%;
}
.footer .status .image {
  width: 20px;
  position: relative;
  top: 5px;
}
</style>